import React, { useState } from 'react';
import { useApp } from '../../context/AppContext';

const AddContentModal = () => {
  const { state, closeModal, createContent, showToast } = useApp();
  
  const [formData, setFormData] = useState({
    type: '',
    title: '',
    description: '',
    category: '',
    difficulty: 'Intermediate',
    tags: '',
    content: '',
    author: '',
    duration: 60,
    focus: 'Full Body'
  });

  const [errors, setErrors] = useState({});

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    // Clear error when field is edited
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const validateForm = () => {
    const newErrors = {};
    
    if (!formData.type) newErrors.type = 'Content type is required';
    if (!formData.title.trim()) newErrors.title = 'Title is required';
    if (!formData.description.trim()) newErrors.description = 'Description is required';
    if (!formData.category) newErrors.category = 'Category is required';
    if (!formData.content.trim()) newErrors.content = 'Content is required';
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) {
      showToast('Please fill all required fields', 'error');
      return;
    }

    try {
      await createContent(formData);
      
      // Reset form
      setFormData({
        type: '',
        title: '',
        description: '',
        category: '',
        difficulty: 'Intermediate',
        tags: '',
        content: '',
        author: '',
        duration: 60,
        focus: 'Full Body'
      });
      setErrors({});
      
    } catch (error) {
      console.error('Form submission error:', error);
    }
  };

  const categories = [
    'Bodybuilding', 'Calisthenics', 'Weight Loss', 'Muscle Gain',
    'Strength Training', 'Cardio', 'Nutrition', 'Recovery'
  ];

  if (!state.modals.addContent) return null;

  return (
    <div className="modal active" id="addContentModal">
      <div className="modal-content">
        <div className="modal-header">
          <h2 className="modal-title">Create New Content</h2>
          <button className="modal-close" onClick={() => closeModal('addContent')}>
            &times;
          </button>
        </div>
        <div className="modal-body">
          <form id="contentForm" onSubmit={handleSubmit}>
            <div className="form-group">
              <label className="form-label">Content Type *</label>
              <select 
                className="form-control" 
                name="type"
                value={formData.type}
                onChange={handleChange}
                required
              >
                <option value="">Select Type</option>
                <option value="fitness">Fitness Plan</option>
                <option value="workout">Workout Plan</option>
                <option value="discussion">Discussion</option>
              </select>
              {errors.type && <div className="error-text" style={{ color: 'var(--danger)', fontSize: '0.85rem', marginTop: '5px' }}>{errors.type}</div>}
            </div>

            <div className="form-group">
              <label className="form-label">Title *</label>
              <input 
                type="text" 
                className="form-control" 
                name="title"
                value={formData.title}
                onChange={handleChange}
                required 
                placeholder="Enter a descriptive title"
              />
              {errors.title && <div className="error-text" style={{ color: 'var(--danger)', fontSize: '0.85rem', marginTop: '5px' }}>{errors.title}</div>}
            </div>

            <div className="form-group">
              <label className="form-label">Description *</label>
              <textarea 
                className="form-control" 
                name="description"
                value={formData.description}
                onChange={handleChange}
                required 
                rows="3" 
                placeholder="Brief description of your content"
              />
              {errors.description && <div className="error-text" style={{ color: 'var(--danger)', fontSize: '0.85rem', marginTop: '5px' }}>{errors.description}</div>}
            </div>

            <div className="form-row">
              <div className="form-group">
                <label className="form-label">Category *</label>
                <select 
                  className="form-control" 
                  name="category"
                  value={formData.category}
                  onChange={handleChange}
                  required
                >
                  <option value="">Select Category</option>
                  {categories.map(category => (
                    <option key={category} value={category}>{category}</option>
                  ))}
                </select>
                {errors.category && <div className="error-text" style={{ color: 'var(--danger)', fontSize: '0.85rem', marginTop: '5px' }}>{errors.category}</div>}
              </div>
              <div className="form-group">
                <label className="form-label">Difficulty Level</label>
                <select 
                  className="form-control" 
                  name="difficulty"
                  value={formData.difficulty}
                  onChange={handleChange}
                >
                  <option value="Beginner">Beginner</option>
                  <option value="Intermediate">Intermediate</option>
                  <option value="Advanced">Advanced</option>
                </select>
              </div>
            </div>

            {formData.type === 'workout' && (
              <div className="form-group" id="workoutFields">
                <div className="form-row">
                  <div className="form-group">
                    <label className="form-label">Duration (minutes)</label>
                    <input 
                      type="number" 
                      className="form-control" 
                      name="duration"
                      value={formData.duration}
                      onChange={handleChange}
                      placeholder="60"
                    />
                  </div>
                  <div className="form-group">
                    <label className="form-label">Focus Area</label>
                    <input 
                      type="text" 
                      className="form-control" 
                      name="focus"
                      value={formData.focus}
                      onChange={handleChange}
                      placeholder="e.g., Upper Body, Legs, Core"
                    />
                  </div>
                </div>
              </div>
            )}

            <div className="form-group">
              <label className="form-label">Tags (comma separated)</label>
              <input 
                type="text" 
                className="form-control" 
                name="tags"
                value={formData.tags}
                onChange={handleChange}
                placeholder="chest, push, hypertrophy, strength"
              />
            </div>

            <div className="form-group">
              <label className="form-label">Detailed Content *</label>
              <textarea 
                className="form-control" 
                name="content"
                value={formData.content}
                onChange={handleChange}
                required 
                rows="6" 
                placeholder="Enter your detailed content here..."
              />
              {errors.content && <div className="error-text" style={{ color: 'var(--danger)', fontSize: '0.85rem', marginTop: '5px' }}>{errors.content}</div>}
            </div>

            <div className="form-group">
              <label className="form-label">Your Name (optional)</label>
              <input 
                type="text" 
                className="form-control" 
                name="author"
                value={formData.author}
                onChange={handleChange}
                placeholder="Anonymous"
              />
            </div>

            <button type="submit" className="btn btn-primary" style={{ width: '100%', padding: '14px' }}>
              <i className="fas fa-paper-plane"></i>
              <span>Publish Content</span>
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default AddContentModal;