// Format date
export const formatDate = (dateString) => {
  if (!dateString) return 'Unknown date';
  
  const date = new Date(dateString);
  const now = new Date();
  const diffMs = now - date;
  const diffDays = Math.floor(diffMs / (1000 * 60 * 60 * 24));
  
  if (diffDays === 0) return 'Today';
  if (diffDays === 1) return 'Yesterday';
  if (diffDays < 7) return `${diffDays} days ago`;
  if (diffDays < 30) return `${Math.floor(diffDays / 7)} weeks ago`;
  
  return date.toLocaleDateString('en-US', { 
    month: 'short', 
    day: 'numeric',
    year: 'numeric'
  });
};

// Escape HTML to prevent XSS
export const escapeHtml = (text) => {
  if (!text) return '';
  const div = document.createElement('div');
  div.textContent = text;
  return div.innerHTML;
};

// Calculate read time
export const calculateReadTime = (content) => {
  if (!content) return 1;
  const wordCount = content.split(/\s+/).length;
  return Math.max(1, Math.ceil(wordCount / 200));
};

// Format content for display
export const formatContentForDisplay = (content, type) => {
  if (!content) return '<p>No content available.</p>';
  
  let formatted = content;
  
  // Format workout tables
  if (type === 'workout') {
    const lines = content.split('\n');
    let result = '';
    let inTable = false;
    let tableRows = [];
    
    for (let line of lines) {
      if (line.trim().match(/^\|.*\|$/)) {
        if (!inTable) {
          inTable = true;
          tableRows = [];
        }
        tableRows.push(line.trim());
      } else {
        if (inTable) {
          result += formatAsTable(tableRows);
          inTable = false;
        }
        result += line + '\n';
      }
    }
    
    if (inTable) {
      result += formatAsTable(tableRows);
    }
    
    formatted = result;
  }
  
  // Basic formatting
  formatted = formatted
    .replace(/\n\s*\n/g, '</p><p>')
    .replace(/\n/g, '<br>');
  
  if (!formatted.startsWith('<p>')) {
    formatted = '<p>' + formatted + '</p>';
  }
  
  // Headers
  formatted = formatted.replace(/^#{1,6}\s+(.+)$/gm, (match, p1) => {
    const level = match.match(/#/g).length;
    return `<h${level} style="margin-top: 1.5rem; margin-bottom: 0.8rem; color: var(--primary);">${p1}</h${level}>`;
  });
  
  // Lists
  formatted = formatted.replace(/^\s*[-*]\s+(.+)$/gm, '<li>$1</li>');
  
  // Bold
  formatted = formatted.replace(/\*\*(.+?)\*\*/g, '<strong>$1</strong>');
  
  // Italic
  formatted = formatted.replace(/\*(.+?)\*/g, '<em>$1</em>');
  
  return formatted;
};

// Format as table
const formatAsTable = (rows) => {
  if (rows.length < 2) return '';
  
  let html = '<div style="overflow-x: auto; margin: 1.5rem 0;">';
  html += '<table class="workout-table">';
  
  // Header row
  const headerCells = rows[0].split('|').filter(cell => cell.trim());
  html += '<tr>';
  headerCells.forEach(cell => {
    html += `<th>${escapeHtml(cell.trim())}</th>`;
  });
  html += '</tr>';
  
  // Data rows
  for (let i = 1; i < rows.length; i++) {
    const cells = rows[i].split('|').filter(cell => cell.trim());
    html += '<tr>';
    cells.forEach(cell => {
      html += `<td>${escapeHtml(cell.trim())}</td>`;
    });
    html += '</tr>';
  }
  
  html += '</table></div>';
  return html;
};

// Get type icon
export const getTypeIcon = (type) => {
  switch(type) {
    case 'fitness': return 'clipboard-list';
    case 'workout': return 'dumbbell';
    case 'discussion': return 'comments';
    default: return 'file-alt';
  }
};

// Get type name
export const getTypeName = (type) => {
  switch(type) {
    case 'fitness': return 'Fitness Plan';
    case 'workout': return 'Workout Plan';
    case 'discussion': return 'Discussion';
    default: return 'Content';
  }
};

// Get badge class
export const getBadgeClass = (type) => {
  switch(type) {
    case 'fitness': return 'badge-fitness';
    case 'workout': return 'badge-workout';
    case 'discussion': return 'badge-discussion';
    default: return '';
  }
};