import React from 'react';
import { useApp } from '../../context/AppContext';

const DeleteConfirmationModal = () => {
  const { state, closeModal, deleteContent } = useApp();

  if (!state.modals.deleteConfirm) return null;

  const handleConfirm = () => {
    deleteContent();
  };

  const handleCancel = () => {
    closeModal('deleteConfirm');
  };

  return (
    <div className="modal active" id="deleteConfirmModal">
      <div className="modal-content" style={{ maxWidth: '400px' }}>
        <div className="modal-header">
          <h2 className="modal-title"> </h2>
          <button className="modal-close" onClick={handleCancel}>
            &times;
          </button>
        </div>
        <div className="modal-body">
          <div style={{ textAlign: 'center', padding: '20px 10px' }}>
            <div style={{ fontSize: '3rem', color: 'var(--danger)', marginBottom: '15px' }}>
              <i className="fas fa-trash-alt"></i>
            </div>
            <h3 style={{ marginBottom: '10px', color: 'var(--dark)' }}>  ?</h3>
            <p style={{ marginBottom: '20px', color: 'var(--gray)', fontSize: '0.95rem' }}>
                   <br />
                   
            </p>
            <div style={{ background: 'rgba(239, 68, 68, 0.1)', padding: '12px', borderRadius: '8px', margin: '20px 0' }}>
              <p style={{ color: 'var(--danger)', margin: 0, fontSize: '0.9rem' }}>
                <i className="fas fa-exclamation-circle"></i>
                :      
              </p>
            </div>
            <div style={{ display: 'flex', gap: '10px', justifyContent: 'center', marginTop: '25px' }}>
              <button className="btn btn-secondary" onClick={handleCancel} style={{ minWidth: '100px' }}>
                <i className="fas fa-times"></i>
                <span></span>
              </button>
              <button 
                className="btn" 
                onClick={handleConfirm} 
                style={{ background: 'var(--danger)', color: 'white', minWidth: '100px' }}
              >
                <i className="fas fa-trash"></i>
                <span></span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DeleteConfirmationModal;