import React from 'react';
import { AppProvider } from './context/AppContext';
import Navbar from './components/Navbar';
import Hero from './components/Hero';
import ContentGrid from './components/ContentGrid';
import Footer from './components/Footer';
import AddContentModal from './components/modals/AddContentModal';
import ViewContentModal from './components/modals/ViewContentModal';
import DeleteConfirmationModal from './components/modals/DeleteConfirmationModal';
import Toast from './components/ui/Toast';
import LoadingOverlay from './components/ui/LoadingOverlay';
import { useApp } from './context/AppContext';

function AppContent() {
  const { state, setTab, searchContent } = useApp();

  const handleSearch = (e) => {
    searchContent(e.target.value);
  };

  const tabs = [
    { id: 'all', icon: 'th-large', label: 'All' },
    { id: 'fitness', icon: 'clipboard-list', label: 'Fitness' },
    { id: 'workout', icon: 'dumbbell', label: 'Workouts' },
    { id: 'discussion', icon: 'comments', label: 'Discussions' }
  ];

  return (
    <div className="app">
      <Navbar />
      
      <Hero />
      
      <main className="container">
        <div className="search-container">
          <div className="search-bar">
            <i className="fas fa-search search-icon"></i>
            <input 
              type="text" 
              className="search-input" 
              id="searchInput"
              placeholder="Search fitness plans, workouts, discussions..." 
              onChange={handleSearch}
            />
          </div>
        </div>

        <div className="tabs" id="contentTabs">
          {tabs.map(tab => (
            <button 
              key={tab.id}
              className={`tab-btn ${state.currentTab === tab.id ? 'active' : ''}`}
              onClick={() => setTab(tab.id)}
            >
              <i className={`fas fa-${tab.icon}`}></i>
              <span>{tab.label}</span>
            </button>
          ))}
        </div>

        <ContentGrid />
      </main>

      <Footer />
      
      <AddContentModal />
      <ViewContentModal />
      <DeleteConfirmationModal />
      <Toast />
      <LoadingOverlay />
    </div>
  );
}

function App() {
  return (
    <AppProvider>
      <AppContent />
    </AppProvider>
  );
}

export default App;