import React, { createContext, useState, useContext, useEffect, useCallback } from 'react';
import { supabase, contentService, setupRealtimeSubscriptions } from '../services/supabase';

const AppContext = createContext();

export const useApp = () => useContext(AppContext);

export const AppProvider = ({ children }) => {
  const [state, setState] = useState({
    currentTab: 'all',
    searchQuery: '',
    allContent: [],
    filteredContent: [],
    isLoading: false,
    isDarkMode: localStorage.getItem('darkMode') === 'true',
    viewStats: {
      totalPlans: 0,
      totalWorkouts: 0,
      totalDiscussions: 0,
      totalViews: 0
    },
    modals: {
      addContent: false,
      viewContent: false,
      deleteConfirm: false
    },
    selectedContent: null,
    pendingDelete: null,
    toast: {
      show: false,
      message: '',
      type: 'success'
    },
    mobileMenuOpen: false
  });

  // Update statistics
  const updateStatistics = useCallback((content) => {
    const stats = {
      totalPlans: content.filter(item => item.type === 'fitness').length,
      totalWorkouts: content.filter(item => item.type === 'workout').length,
      totalDiscussions: content.filter(item => item.type === 'discussion').length,
      totalViews: content.reduce((sum, item) => sum + (item.views || 0), 0)
    };

    setState(prev => ({
      ...prev,
      viewStats: stats
    }));
  }, []);

  // Load all content
  const loadAllContent = useCallback(async () => {
    setState(prev => ({ ...prev, isLoading: true }));

    try {
      const content = await contentService.loadAllContent();
      
      // Load liked status from localStorage
      const contentWithLikes = content.map(item => {
        const liked = localStorage.getItem(`liked_${item.type}_${item.id}`) === 'true';
        return { ...item, liked: liked };
      });

      setState(prev => ({
        ...prev,
        allContent: contentWithLikes,
        filteredContent: filterContent(contentWithLikes, prev.currentTab, prev.searchQuery),
        isLoading: false
      }));

      updateStatistics(contentWithLikes);
    } catch (error) {
      console.error('Error loading content:', error);
      showToast('Failed to load content. Please refresh.', 'error');
      setState(prev => ({ ...prev, isLoading: false }));
    }
  }, [updateStatistics]);

  // Filter content
  const filterContent = (content, tab, search) => {
    return content.filter(item => {
      if (tab !== 'all' && item.type !== tab) return false;
      
      if (search) {
        const query = search.toLowerCase();
        const searchable = [
          item.title,
          item.description,
          item.body,
          item.category,
          item.tags,
          item.author
        ].filter(Boolean).join(' ').toLowerCase();
        
        return searchable.includes(query);
      }
      
      return true;
    });
  };

  // Set current tab
  const setTab = (tab) => {
    setState(prev => ({
      ...prev,
      currentTab: tab,
      filteredContent: filterContent(prev.allContent, tab, prev.searchQuery)
    }));
  };

  // Search content
  const searchContent = (query) => {
    setState(prev => ({
      ...prev,
      searchQuery: query,
      filteredContent: filterContent(prev.allContent, prev.currentTab, query)
    }));
  };

  // Toggle theme
  const toggleTheme = () => {
    const newDarkMode = !state.isDarkMode;
    document.body.classList.toggle('dark-mode', newDarkMode);
    localStorage.setItem('darkMode', newDarkMode);
    
    setState(prev => ({ ...prev, isDarkMode: newDarkMode }));
    showToast(`Switched to ${newDarkMode ? 'dark' : 'light'} mode`);
  };

  // Modal controls
  const openModal = (modalName, data = null) => {
    setState(prev => ({
      ...prev,
      modals: { ...prev.modals, [modalName]: true },
      selectedContent: data,
      mobileMenuOpen: false
    }));
  };

  const closeModal = (modalName) => {
    setState(prev => ({
      ...prev,
      modals: { ...prev.modals, [modalName]: false },
      selectedContent: null,
      pendingDelete: null
    }));
  };

  // Toast notification
  const showToast = (message, type = 'success') => {
    setState(prev => ({ ...prev, toast: { show: true, message, type } }));
    
    setTimeout(() => {
      setState(prev => ({ ...prev, toast: { ...prev.toast, show: false } }));
    }, 4000);
  };

  // Toggle like
  const toggleLike = async (type, id) => {
    const itemIndex = state.allContent.findIndex(item => 
      item.id === id && item.type === type
    );
    
    if (itemIndex === -1) return;
    
    const item = state.allContent[itemIndex];
    const currentlyLiked = item.liked || false;
    const newLiked = !currentlyLiked;
    const currentLikes = item.likes || 0;
    const newLikes = newLiked ? currentLikes + 1 : Math.max(0, currentLikes - 1);
    
    // Update local state immediately for responsive UI
    const updatedContent = [...state.allContent];
    updatedContent[itemIndex] = { 
      ...item, 
      liked: newLiked, 
      likes: newLikes 
    };
    
    setState(prev => ({
      ...prev,
      allContent: updatedContent,
      filteredContent: filterContent(updatedContent, prev.currentTab, prev.searchQuery)
    }));
    
    // Save to localStorage
    localStorage.setItem(`liked_${type}_${id}`, newLiked);
    
    try {
      await contentService.updateLikes(type, id, newLikes);
      showToast(newLiked ? 'Liked!' : 'Unliked!', 'success');
    } catch (error) {
      console.error('Error updating likes:', error);
      // Revert if error
      updatedContent[itemIndex] = item;
      setState(prev => ({
        ...prev,
        allContent: updatedContent,
        filteredContent: filterContent(updatedContent, prev.currentTab, prev.searchQuery)
      }));
      showToast('Failed to update like', 'error');
    }
  };

  // View content
  const viewContent = async (type, id) => {
    try {
      const item = state.allContent.find(item => item.id === id && item.type === type);
      if (!item) return;
      
      // Increment views
      const updatedItem = await contentService.incrementViews(type, id, item.views);
      
      // Update local state
      const updatedContent = state.allContent.map(content =>
        content.id === id && content.type === type
          ? { ...content, views: updatedItem.views }
          : content
      );
      
      setState(prev => ({
        ...prev,
        allContent: updatedContent,
        filteredContent: filterContent(updatedContent, prev.currentTab, prev.searchQuery),
        selectedContent: { ...item, views: updatedItem.views }
      }));
      
      updateStatistics(updatedContent);
      openModal('viewContent', { ...item, views: updatedItem.views });
      
    } catch (error) {
      console.error('Error viewing content:', error);
      showToast('Failed to load content', 'error');
    }
  };

  // Create content
  const createContent = async (formData) => {
    try {
      const newContent = await contentService.createContent(formData.type, formData);
      
      // Add to local state
      const updatedContent = [{ ...newContent, type: formData.type }, ...state.allContent];
      
      setState(prev => ({
        ...prev,
        allContent: updatedContent,
        filteredContent: filterContent(updatedContent, prev.currentTab, prev.searchQuery)
      }));
      
      updateStatistics(updatedContent);
      closeModal('addContent');
      showToast('Content published successfully!', 'success');
      
    } catch (error) {
      console.error('Error creating content:', error);
      showToast(`Failed to publish: ${error.message}`, 'error');
    }
  };

  // Delete content
  const deleteContent = async () => {
    if (!state.pendingDelete) return;
    
    const { type, id } = state.pendingDelete;
    
    try {
      await contentService.deleteContent(type, id);
      
      // Remove from local state
      const updatedContent = state.allContent.filter(item => 
        !(item.id === id && item.type === type)
      );
      
      // Remove from localStorage
      localStorage.removeItem(`liked_${type}_${id}`);
      
      setState(prev => ({
        ...prev,
        allContent: updatedContent,
        filteredContent: filterContent(updatedContent, prev.currentTab, prev.searchQuery),
        pendingDelete: null
      }));
      
      updateStatistics(updatedContent);
      closeModal('deleteConfirm');
      showToast('    !', 'success');
      
    } catch (error) {
      console.error('Error deleting content:', error);
      showToast('      ', 'error');
    }
  };

  // Set pending delete
  const setPendingDelete = (type, id) => {
    setState(prev => ({
      ...prev,
      pendingDelete: { type, id }
    }));
    openModal('deleteConfirm');
  };

  // Toggle mobile menu
  const toggleMobileMenu = () => {
    setState(prev => ({ ...prev, mobileMenuOpen: !prev.mobileMenuOpen }));
  };

  // Close mobile menu
  const closeMobileMenu = () => {
    setState(prev => ({ ...prev, mobileMenuOpen: false }));
  };

  // Initialize
  useEffect(() => {
    // Set initial theme
    if (state.isDarkMode) {
      document.body.classList.add('dark-mode');
    }

    // Load content
    loadAllContent();

    // Setup realtime subscriptions
    setupRealtimeSubscriptions(loadAllContent);

    // Setup scroll listener for navbar
    const handleScroll = () => {
      const nav = document.querySelector('nav');
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
      
      if (nav) {
        if (scrollTop > 10) {
          nav.classList.add('scrolled');
        } else {
          nav.classList.remove('scrolled');
        }
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [loadAllContent]);

  const value = {
    state,
    setTab,
    searchContent,
    toggleTheme,
    openModal,
    closeModal,
    showToast,
    toggleLike,
    viewContent,
    createContent,
    deleteContent,
    setPendingDelete,
    toggleMobileMenu,
    closeMobileMenu,
    loadAllContent
  };

  return <AppContext.Provider value={value}>{children}</AppContext.Provider>;
};