import React from 'react';
import { useApp } from '../../context/AppContext';
import { 
  formatDate, 
  escapeHtml, 
  calculateReadTime, 
  formatContentForDisplay,
  getTypeIcon,
  getTypeName 
} from '../../utils/helpers';

const ViewContentModal = () => {
  const { state, closeModal, toggleLike, showToast } = useApp();
  
  if (!state.modals.viewContent || !state.selectedContent) return null;

  const content = state.selectedContent;
  const type = content.type;
  const liked = content.liked || false;
  
  const tagsArray = Array.isArray(content.tags) 
    ? content.tags
    : (content.tags && typeof content.tags === 'string')
      ? content.tags.split(',').map(tag => tag.trim())
      : [];

  const readTime = calculateReadTime(content.body || content.content);
  const formattedContent = formatContentForDisplay(content.body || content.content || '', type);

  const handleShare = () => {
    const url = window.location.href;
    const title = content.title || 'Check out this content!';
    
    if (navigator.share) {
      navigator.share({
        title: title,
        text: 'Check out this fitness content on Aesthetic Academy',
        url: url
      }).catch(console.error);
    } else {
      navigator.clipboard.writeText(url).then(() => {
        showToast('Link copied to clipboard!', 'success');
      }).catch(console.error);
    }
  };

  const handlePrint = () => {
    const printWindow = window.open('', '_blank');
    printWindow.document.write(`
      <html>
        <head>
          <title>${escapeHtml(content.title)}</title>
          <style>
            body { font-family: Arial, sans-serif; padding: 20px; max-width: 800px; margin: 0 auto; }
            h1 { color: #333; border-bottom: 2px solid #6366f1; padding-bottom: 10px; }
            .meta { color: #666; margin: 10px 0 20px 0; }
            .content { line-height: 1.6; }
            .footer { margin-top: 30px; padding-top: 20px; border-top: 1px solid #ddd; color: #666; font-size: 0.9em; }
          </style>
        </head>
        <body>
          <h1>${escapeHtml(content.title)}</h1>
          <div class="meta">
            By ${escapeHtml(content.author || 'Anonymous')}  
            ${formatDate(content.created_at)}  
            ${readTime} min read
          </div>
          <div class="content">${formattedContent}</div>
          <div class="footer">
            Printed from Aesthetic Academy  ${new Date().toLocaleDateString()}
          </div>
        </body>
      </html>
    `);
    printWindow.document.close();
    printWindow.print();
  };

  const handleLike = () => {
    toggleLike(type, content.id);
  };

  return (
    <div className="modal active view-content-modal" id="viewContentModal">
      <div className="modal-content">
        <div className="modal-header">
          <h2 className="modal-title">{escapeHtml(content.title)}</h2>
          <button className="modal-close" onClick={() => closeModal('viewContent')}>
            &times;
          </button>
        </div>
        <div className="modal-body">
          <div className="reading-view-container">
            {/* Header Section */}
            <div className="reading-header">
              <span className="category-badge">
                <i className={`fas fa-${getTypeIcon(type)}`}></i>
                {getTypeName(type)}
              </span>
              
              <h1 className="reading-title">{escapeHtml(content.title)}</h1>
              
              <div className="reading-meta">
                <div className="meta-item">
                  <i className="fas fa-user-circle"></i>
                  <span>{escapeHtml(content.author || 'Anonymous')}</span>
                </div>
                <div className="meta-item">
                  <i className="fas fa-calendar-alt"></i>
                  <span>{formatDate(content.created_at)}</span>
                </div>
                <div className="meta-item">
                  <i className="fas fa-folder"></i>
                  <span>{escapeHtml(content.category || 'Uncategorized')}</span>
                </div>
                {content.difficulty && (
                  <div className="meta-item">
                    <i className="fas fa-chart-line"></i>
                    <span>{escapeHtml(content.difficulty)}</span>
                  </div>
                )}
              </div>
            </div>

            {/* Content Section */}
            <div className="reading-content">
              <div className="content-wrapper">
                <div className="read-time">
                  <i className="fas fa-clock"></i>
                  <span>{readTime} min read</span>
                </div>
                
                <div className="content-block">
                  <div 
                    className="content-text"
                    dangerouslySetInnerHTML={{ __html: formattedContent }}
                  />
                </div>
                
                {tagsArray.length > 0 && (
                  <div className="content-tags">
                    {tagsArray.map((tag, index) => (
                      <span key={index} className="reading-tag">
                        <i className="fas fa-hashtag"></i>
                        {escapeHtml(tag)}
                      </span>
                    ))}
                  </div>
                )}
                
                <div className="author-box">
                  <div className="author-avatar">
                    {escapeHtml((content.author || 'A').charAt(0).toUpperCase())}
                  </div>
                  <div className="author-info">
                    <h4>{escapeHtml(content.author || 'Anonymous')}</h4>
                    <p>Shared on Aesthetic Academy</p>
                  </div>
                </div>
              </div>
            </div>

            {/* Actions Section */}
            <div className="reading-actions">
              <div className="action-buttons">
                <button className="btn btn-primary" onClick={handleShare}>
                  <i className="fas fa-share-alt"></i>
                  Share
                </button>
                <button className="btn btn-outline" onClick={handlePrint}>
                  <i className="fas fa-print"></i>
                  Print
                </button>
                <button 
                  className={`reading-btn-like ${liked ? 'active' : ''}`} 
                  onClick={handleLike}
                >
                  <i className="fas fa-heart"></i>
                  <span>{content.likes || 0}</span>
                </button>
              </div>
              
              <div className="reading-stats">
                <div className="reading-stat">
                  <i className="fas fa-eye"></i>
                  <span>{content.views || 0} views</span>
                </div>
                <div className="reading-stat">
                  <i className="fas fa-comment"></i>
                  <span>0 comments</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ViewContentModal;