import React from 'react';
import { useApp } from '../context/AppContext';
import Stats from './Stats';

const Hero = () => {
  const { state, openModal } = useApp();

  return (
    <section className="hero">
      <div className="container">
        <h1>Build Your Perfect Physique</h1>
        <p>Share fitness plans, workout routines, and discussions with the global community. No signup required.</p>
        
        <div className="hero-buttons">
          <button 
            className="btn btn-primary" 
            onClick={() => openModal('addContent')}
          >
            <i className="fas fa-rocket"></i>
            <span className="btn-text">Start Creating</span>
          </button>
          <button 
            className="btn btn-secondary"
            onClick={() => {
              // Scroll to content
              document.querySelector('.content-grid')?.scrollIntoView({ behavior: 'smooth' });
            }}
          >
            <i className="fas fa-compass"></i>
            <span className="btn-text">Explore Community</span>
          </button>
        </div>
        
        <Stats stats={state.viewStats} />
      </div>
    </section>
  );
};

export default Hero;