import React from 'react';

const Stats = ({ stats }) => {
  return (
    <div className="stats">
      <div className="stat-card">
        <div className="stat-number">{stats.totalPlans}</div>
        <div className="stat-label">Fitness Plans</div>
      </div>
      <div className="stat-card">
        <div className="stat-number">{stats.totalWorkouts}</div>
        <div className="stat-label">Workout Plans</div>
      </div>
      <div className="stat-card">
        <div className="stat-number">{stats.totalDiscussions}</div>
        <div className="stat-label">Discussions</div>
      </div>
      <div className="stat-card">
        <div className="stat-number">{stats.totalViews}</div>
        <div className="stat-label">Total Views</div>
      </div>
    </div>
  );
};

export default Stats;