import React, { useState } from 'react';
import { useApp } from '../context/AppContext';
import { 
  formatDate, 
  escapeHtml, 
  getBadgeClass 
} from '../utils/helpers';

const ContentCard = ({ item }) => {
  const { 
    viewContent, 
    toggleLike, 
    setPendingDelete,
    showToast 
  } = useApp();
  
  const [dropdownOpen, setDropdownOpen] = useState(false);

  const handleCardClick = (e) => {
    if (!e.target.closest('.card-options') && 
        !e.target.closest('.btn-like') && 
        !e.target.closest('.dropdown-toggle')) {
      viewContent(item.type, item.id);
    }
  };

  const handleLikeClick = (e) => {
    e.stopPropagation();
    toggleLike(item.type, item.id);
  };

  const toggleDropdown = (e) => {
    e.stopPropagation();
    setDropdownOpen(!dropdownOpen);
  };

  const closeDropdown = () => {
    setDropdownOpen(false);
  };

  const handleReport = () => {
    closeDropdown();
    const reason = prompt("   ():\n\n1.  \n2. \n3.  \n4. ", "");
    
    if (reason && reason.trim() !== '') {
      console.log(`Report submitted for ${item.type}-${item.id}: ${reason}`);
      showToast('!    ', 'success');
    } else {
      showToast('   ', 'warning');
    }
  };

  const tags = Array.isArray(item.tags) 
    ? item.tags.slice(0, 3)
    : (item.tags && typeof item.tags === 'string')
      ? item.tags.split(',').slice(0, 3).map(tag => tag.trim())
      : [];

  const badgeClass = getBadgeClass(item.type);

  return (
    <div 
      className="content-card" 
      data-id={item.id} 
      data-type={item.type}
      onClick={handleCardClick}
    >
      <span className={`card-badge ${badgeClass}`}>
        {item.type.charAt(0).toUpperCase() + item.type.slice(1)}
      </span>
      
      <div className="card-header">
        <h3 className="card-title">{escapeHtml(item.title || 'Untitled')}</h3>
        <div className="card-meta">
          <span><i className="fas fa-folder"></i> {escapeHtml(item.category || 'Uncategorized')}</span>
          <span><i className="fas fa-user"></i> {escapeHtml(item.author || 'Anonymous')}</span>
          <span><i className="fas fa-calendar"></i> {formatDate(item.created_at)}</span>
        </div>
      </div>
      
      <div className="card-content">
        <p>{escapeHtml(item.description || '').substring(0, 120)}{item.description && item.description.length > 119 ? '...' : ''}</p>
        {item.type === 'workout' && (
          <div style={{ fontSize: '0.8rem', color: 'var(--gray)', marginTop: '6px' }}>
            <i className="fas fa-clock"></i> {item.duration || 60} min | 
            <i className="fas fa-dumbbell"></i> {item.focus || 'Full Body'}
          </div>
        )}
      </div>
      
      {tags.length > 0 && (
        <div className="card-tags">
          {tags.map((tag, index) => (
            <span key={index} className="tag">
              {escapeHtml(tag)}
            </span>
          ))}
        </div>
      )}
      
      <div className="card-footer">
        <div className="card-stats">
          <span className="card-stat">
            <i className="fas fa-eye"></i> {item.views || 0}
          </span>
          <span className="card-stat">
            <button 
              className={`btn-like ${item.liked ? 'liked' : ''}`}
              onClick={handleLikeClick}
              data-id={item.id} 
              data-type={item.type}
            >
              <i className="fas fa-heart"></i>
              <span className="like-count">{item.likes || 0}</span>
            </button>
          </span>
        </div>
        <div className="card-options">
          <button className="dropdown-toggle" onClick={toggleDropdown}>
            <i className="fas fa-ellipsis-v"></i>
          </button>
          {dropdownOpen && (
            <>
              <div className="dropdown-backdrop" onClick={closeDropdown}></div>
              <div className="dropdown-menu show">
                <button 
                  className="dropdown-item view-btn" 
                  onClick={(e) => {
                    e.stopPropagation();
                    viewContent(item.type, item.id);
                    closeDropdown();
                  }}
                >
                  <i className="fas fa-expand-alt"></i> View Details
                </button>
                <button 
                  className="dropdown-item" 
                  onClick={(e) => {
                    e.stopPropagation();
                    setPendingDelete(item.type, item.id);
                    closeDropdown();
                  }}
                >
                  <i className="fas fa-trash"></i> Delete
                </button>
                <div className="dropdown-divider"></div>
                <button 
                  className="dropdown-item text-danger" 
                  onClick={(e) => {
                    e.stopPropagation();
                    handleReport();
                  }}
                >
                  <i className="fas fa-flag"></i> Report Content
                </button>
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default ContentCard;