import React from 'react';
import { useApp } from '../context/AppContext';

const Footer = () => {
  const { setTab, openModal } = useApp();

  const footerLinks = [
    { label: 'All Content', tab: 'all' },
    { label: 'Fitness Plans', tab: 'fitness' },
    { label: 'Workout Plans', tab: 'workout' },
    { label: 'Discussions', tab: 'discussion' }
  ];

  const resourceLinks = [
    { label: 'Fitness Guide', href: '#' },
    { label: 'Workout Templates', href: '#' },
    { label: 'Nutrition Tips', href: '#' },
    { label: 'Community Guidelines', href: '#' }
  ];

  const socialLinks = [
    { icon: 'instagram', href: '#' },
    { icon: 'twitter', href: '#' },
    { icon: 'youtube', href: '#' },
    { icon: 'discord', href: '#' }
  ];

  return (
    <footer>
      <div className="container">
        <div className="footer-content">
          <div className="footer-section">
            <h3>Aesthetic Academy</h3>
            <p>The ultimate platform for sharing fitness knowledge. No signup required. All content is publicly accessible and shareable.</p>
            <div className="social-links">
              {socialLinks.map((social, index) => (
                <a key={index} href={social.href} aria-label={social.icon}>
                  <i className={`fab fa-${social.icon}`}></i>
                </a>
              ))}
            </div>
          </div>
          
          <div className="footer-section">
            <h3>Quick Links</h3>
            <div className="footer-links">
              {footerLinks.map((link, index) => (
                <a 
                  key={index} 
                  href="#" 
                  onClick={(e) => {
                    e.preventDefault();
                    setTab(link.tab);
                  }}
                >
                  {link.label}
                </a>
              ))}
              <a 
                href="#"
                onClick={(e) => {
                  e.preventDefault();
                  openModal('addContent');
                }}
              >
                Add Content
              </a>
            </div>
          </div>
          
          <div className="footer-section">
            <h3>Resources</h3>
            <div className="footer-links">
              {resourceLinks.map((link, index) => (
                <a key={index} href={link.href}>
                  {link.label}
                </a>
              ))}
            </div>
          </div>
          
          <div className="footer-section">
            <h3>Powered By</h3>
            <p>Real-time database powered by Supabase. This platform uses anonymous access for seamless sharing.</p>
          </div>
        </div>
        
        <div className="copyright">
          &copy; 2024 Aesthetic Academy. All content is user-generated and publicly shared.
          <br />
          <small>Built with Supabase | Real-time updates enabled | No login required</small>
        </div>
      </div>
    </footer>
  );
};

export default Footer;