export const supabase = createClient(supabaseUrl, supabaseKey);

// Content Service Functions
export const contentService = {
  // Load all content
  async loadAllContent() {
    try {
      const [fitnessPlans, workoutPlans, discussions] = await Promise.all([
        this.loadContent('fitness_plans'),
        this.loadContent('workout_plans'),
        this.loadContent('discussions')
      ]);

      const allContent = [
        ...fitnessPlans.map(item => ({ ...item, type: 'fitness' })),
        ...workoutPlans.map(item => ({ ...item, type: 'workout' })),
        ...discussions.map(item => ({ ...item, type: 'discussion' }))
      ].sort((a, b) => new Date(b.created_at) - new Date(a.created_at));

      return allContent;
    } catch (error) {
      console.error('Error loading all content:', error);
      return [];
    }
  },

  // Load specific table content
  async loadContent(tableName) {
    try {
      const { data, error } = await supabase
        .from(tableName)
        .select('*')
        .order('created_at', { ascending: false });

      if (error) {
        console.error(`Error loading ${tableName}:`, error);
        return [];
      }

      return data || [];
    } catch (error) {
      console.error(`Error loading ${tableName}:`, error);
      return [];
    }
  },

  // Create new content
  async createContent(type, data) {
    let tableName;
    switch (type) {
      case 'fitness':
        tableName = 'fitness_plans';
        break;
      case 'workout':
        tableName = 'workout_plans';
        break;
      case 'discussion':
        tableName = 'discussions';
        break;
      default:
        throw new Error('Invalid content type');
    }

    const insertData = {
      title: data.title,
      description: data.description,
      category: data.category,
      author: data.author || 'Anonymous',
      views: 0,
      likes: 0,
      created_at: new Date().toISOString(),
      body: data.content
    };

    if (data.tags) {
      insertData.tags = data.tags.split(',').map(tag => tag.trim());
    }

    if (type === 'fitness' || type === 'workout') {
      insertData.difficulty = data.difficulty || 'Intermediate';
    }

    if (type === 'workout') {
      insertData.duration = data.duration || 60;
      insertData.focus = data.focus || 'Full Body';
    }

    const { data: result, error } = await supabase
      .from(tableName)
      .insert([insertData])
      .select();

    if (error) throw error;
    return result[0];
  },

  // Update content (for likes, views)
  async updateContent(type, id, updates) {
    let tableName;
    switch (type) {
      case 'fitness':
        tableName = 'fitness_plans';
        break;
      case 'workout':
        tableName = 'workout_plans';
        break;
      case 'discussion':
        tableName = 'discussions';
        break;
      default:
        throw new Error('Invalid content type');
    }

    const { data, error } = await supabase
      .from(tableName)
      .update(updates)
      .eq('id', id)
      .select();

    if (error) throw error;
    return data[0];
  },

  // Delete content
  async deleteContent(type, id) {
    let tableName;
    switch (type) {
      case 'fitness':
        tableName = 'fitness_plans';
        break;
      case 'workout':
        tableName = 'workout_plans';
        break;
      case 'discussion':
        tableName = 'discussions';
        break;
      default:
        throw new Error('Invalid content type');
    }

    const { error } = await supabase
      .from(tableName)
      .delete()
      .eq('id', id);

    if (error) throw error;
    return true;
  },

  // Increment views
  async incrementViews(type, id, currentViews) {
    return await this.updateContent(type, id, { views: (currentViews || 0) + 1 });
  },

  // Update likes
  async updateLikes(type, id, newLikes) {
    return await this.updateContent(type, id, { likes: newLikes });
  }
};

// Real-time subscriptions setup
export const setupRealtimeSubscriptions = (callback) => {
  const tables = ['fitness_plans', 'workout_plans', 'discussions'];

  tables.forEach(table => {
    supabase
      .channel(`${table}_changes`)
      .on('postgres_changes', {
        event: '*',
        schema: 'public',
        table: table
      }, () => {
        callback();
      })
      .subscribe();
  });
};