import React from 'react';
import { useApp } from '../context/AppContext';

const Navbar = () => {
  const { 
    state, 
    setTab, 
    toggleTheme, 
    openModal, 
    toggleMobileMenu,
    closeMobileMenu 
  } = useApp();

  const navItems = [
    { id: 'all', icon: 'th-large', label: 'All Content' },
    { id: 'fitness', icon: 'clipboard-list', label: 'Fitness Plans' },
    { id: 'workout', icon: 'dumbbell', label: 'Workouts' },
    { id: 'discussion', icon: 'comments', label: 'Discussions' }
  ];

  const handleNavClick = (tabId) => {
    setTab(tabId);
    closeMobileMenu();
  };

  return (
    <nav>
      <div className="container nav-container">
        <a href="#" className="logo" onClick={(e) => { e.preventDefault(); setTab('all'); }}>
          <div className="logo-icon">
            <i className="fas fa-dumbbell"></i>
          </div>
          <span className="logo-gradient">Aesthetic Academy</span>
        </a>
        
        <button 
          className="mobile-menu-toggle" 
          onClick={toggleMobileMenu}
        >
          <i className={`fas fa-${state.mobileMenuOpen ? 'times' : 'bars'}`}></i>
        </button>
        
        <div className={`nav-links ${state.mobileMenuOpen ? 'active' : ''}`}>
          {navItems.map(item => (
            <a 
              key={item.id}
              href="#" 
              className={`nav-link ${state.currentTab === item.id ? 'active' : ''}`}
              onClick={(e) => {
                e.preventDefault();
                handleNavClick(item.id);
              }}
            >
              <i className={`fas fa-${item.icon}`}></i>
              <span>{item.label}</span>
            </a>
          ))}
        </div>
        
        <div className="nav-actions">
          <button 
            className="btn btn-primary add-content-btn"
            onClick={() => openModal('addContent')}
          >
            <i className="fas fa-plus"></i>
            <span className="btn-text">Add Content</span>
          </button>
          <button 
            className="theme-toggle" 
            onClick={toggleTheme}
            aria-label="Toggle theme"
          ></button>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;