import React from 'react';
import { useApp } from '../context/AppContext';
import ContentCard from './ContentCard';

const ContentGrid = () => {
  const { state, openModal } = useApp();

  if (state.filteredContent.length === 0) {
    return (
      <div className="empty-state">
        <div className="empty-state-icon">
          <i className="fas fa-clipboard-list"></i>
        </div>
        <h3>No Content Yet</h3>
        <p>Be the first to share your fitness knowledge with the community!</p>
        <button 
          className="btn btn-primary" 
          onClick={() => openModal('addContent')}
        >
          <i className="fas fa-plus"></i>
          <span className="btn-text">Create First Content</span>
        </button>
      </div>
    );
  }

  return (
    <div className="content-grid">
      {state.filteredContent.map(item => (
        <ContentCard key={`${item.type}-${item.id}`} item={item} />
      ))}
    </div>
  );
};

export default ContentGrid;